body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.slick-prev {
  left: 20px;
  top: 40px;
  z-index: 1;
}

.slick-next {
  right: 39px;
  top: 40px;
}

.slick-next:before,
.slick-prev:before {
  font-size: 43px;
  color: #ff2e43;
}

.Rectangle-2068-copy-4 {
  width: clamp(100px,30vw,200px)!important;
  height: 80px;
  margin: 10px 10px 30px;
  display: flex !important;
  align-items: center;
  justify-content: center;
  background-color: #070707;
  text-align: left;
}


.list_button {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 30px 21px 0px 20px;
}
.list_button .left {
  display: flex;
  text-align: left;
}

.list_button .left p {
  margin-left: 31px;
}
.list_button .left .item1 {
  border-bottom: 2px solid #070707;
  font-weight: 900;
  color: #070707;
} 
.list_button .left button {
  background-color: #fff;
  border: none;
  font-family: Montserrat;
  font-size: 18px;
  font-weight: 300;
  margin-left: 31px;
}


.movie-list {
  display: grid;
  grid-column-gap: 15px;
  grid-template-columns: repeat(auto-fit, 360px);
  margin: 30px 21px 0px 20px;
  justify-content: center;
}
.movie-container {
  position: relative;
  z-index: 10;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 30px;
}
.movie-container .image-container {
  position: relative;
  width: 360px;
  height: 204px;
  z-index: auto;
  z-index: initial;
}

.movie-container .movie-background-image {
  position: relative;
  width: 360px;
  height: 204px;
  z-index: -1;
}

.detail {
  display: grid;
  grid-template-columns: 70% 1fr;
  justify-content: center;
  align-items: center;
  margin: 10px auto 10px auto;
}

.right {
  text-align: right;
  justify-content: center;
}

.detail-icon {
  width: 12px;
  height: 12px;
}



.nav-button {
  display: flex;
  justify-content: center;
}
/* CSS */
.button-12 {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 6px 14px;
  font-size: 20px;
  border-radius: 6px;
  border: none;
  margin: 0px 10px 60px 10px;
  background: #070707;
  box-shadow: 0px 0.5px 1px rgba(0, 0, 0, 0.1), inset 0px 0.5px 0.5px rgba(255, 255, 255, 0.5), 0px 0px 0px 0.5px rgba(0, 0, 0, 0.12);
  color: #DFDEDF;
}

.red {
  background-color: #ff2e43;
}

@media only screen and (max-width: 800px) {
  .list_button .left, .right {
    display: none;
  }
}
.logo {
  width: 34px;
  height: 11px;
  margin: 3px 19px 3px 1px;
  border: solid 1px #fff;
  background-color: #fff;
}

.black {
  background-color: black;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
}


.search_box {
  width: clamp(200px,70vw,660px);
  height: 36px;
  background-color: #070707;
  border: 0px;
  border-bottom: 1px solid #8f8f8f;
  padding-left: 26px;
}

input::-webkit-input-placeholder{
  font-family: Montserrat;
  font-size: 14px;
  color: #fff;
}

input:-ms-input-placeholder{
  font-family: Montserrat;
  font-size: 14px;
  color: #fff;
}

input::placeholder{
  font-family: Montserrat;
  font-size: 14px;
  color: #fff;
}

textarea:focus, input:focus{
  outline: none;
}
.overlay {
  position: fixed;
  overflow-y: auto;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 20;
}

.modal {
  position: absolute;
  top: 0px;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  background-color: #FFF;
  z-index: 20;
  display: flex;
  flex-direction: column;
  width: 75%;
  justify-content: center;
}
.info-container {
  margin: 40px 60px 0px 60px;
}
.info {
  width: clamp(100%,50%,50%);
  display: grid;
  grid-template-columns: 1fr 8fr;
  color: #070707;
  font-family: Montserrat;
  font-size: 15.5px;
  font-weight: 300;
}
.info .item1 {
  grid-column-start: 1;
  grid-column-end: 3;
  font-family: Montserrat;
  font-size: 30px;
  font-weight: 500;
}

.info .item3 {
  color: #8c8c8c;
}
.genre-container {
  display: flex;
}
.genre {
  width: 142px;
  height: 40px;
  border: solid 1px var(--black-three);
  background-color: #070707;
  color: #FFF;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 5px 0px 0px;
}
.modal iframe {
  border: none;
  width: 1fr;
  height: 30vw;
  margin: 0px 60px;
}
.horizontal-rule {
  border-bottom: 1px solid #efefef;
  margin: 50px 0px;
}
.summary {
  font-size: 14px;
  font-family: Montserrat;
  padding: 40px clamp(20px,5vw,60px) 100px clamp(20px,5vw,60px);
}
.modal img {
  width: 80%;
  margin: 0px auto 70px auto;
}

.button {
  position: fixed;
  z-index: 20;
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
  color: #FFF;
}

.button.close {
  top: 20px;
  right: 20px;
  font-size: 18px;
}

.button.next {
  top: 50%;
  right: 5%;
  font-size: clamp(10px,5vw,74px);
}

.button.prev {
  top: 50%;
  left: 5%;
  font-size: clamp(10px,5vw,74px);
}
.footer {
  justify-content: left;
  padding: 0px 30px;
  display: flex;
  height: 40px;
}

.footer p {
  font-size: 12px;
  margin-right: 31px;
  color: #fff;
}

.contact {
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
}

.white p {
  color: #070707;
}


